import { Component, OnInit, Input } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { Appointment } from 'src/app/shared/models/appointment';

@Component({
  selector: 'app-overview-list',
  templateUrl: './overview-list.component.html',
  styleUrls: ['./overview-list.component.scss']
})
export class OverviewListComponent implements OnInit {

  @Input() appointment: Appointment;
  checkColor = Appointment.checkColor;
  navigationExtras: NavigationExtras;

  constructor(private router: Router) {
  }

  goToAppointmentList(): void {
    this.router.navigate(['/home/appointments/list'], {fragment: this.appointment.id.toString()});
  }

  ngOnInit() {
    this.navigationExtras = {
      fragment: this.appointment.id.toString()
    };
  }
  checkWidth(): number {
    return window.innerWidth;
  }

}
