import { Component, OnInit } from '@angular/core';
import * as _ from 'lodash';
import { Router } from '@angular/router';
import { Appointment } from 'src/app/shared/models/appointment';
import { AppointmentService } from 'src/app/core/services/appointment-service/appointment.service';
import { SessionUserService } from 'src/app/core/services/session-user-service/session-user.service';
import { NavbarButtonsService } from 'src/app/core/services/navbar-buttons-service/navbar-buttons.service';
import { Roles, AppointmentTypes } from 'src/app/shared/enums/static-strings';
import { User } from 'src/app/shared/models/user';

@Component({selector: 'app-overview', templateUrl: './overview.component.html', styleUrls: ['./overview.component.scss']})
export class OverviewComponent implements OnInit {

  myAppointments: Array < Appointment > = [];
  myShifts: Array < Appointment > = [];
  myAppointmentsCounter = 0;
  myShiftsCounter = 0;

  constructor(private appointmentService: AppointmentService,
    private sessionUser: SessionUserService,
    private navbarButtons: NavbarButtonsService,
    public router: Router) {}

  ngOnInit() {
    this.navbarButtons.changeToOverviewView();
    this
      .appointmentService
      ._allAppointments
      .subscribe(appointments => {
        this.getLastAppointments(appointments);
      });
  }
  checkWidth() {
    return window.innerWidth;
  }
  getLastAppointments(appointments: Appointment[]): any {
    this.myAppointments = [];
    this.myShifts = [];
    appointments.sort((a, b) => a.from.getTime() - b.from.getTime());
    appointments.forEach(appointment => {
      if (this.sessionUser.getUser().entitlements.includes(Roles[Roles.AdminBwst])
      || this.sessionUser.getUser().entitlements.includes(Roles[Roles.UserBwst])) {
        if ((appointment.type === AppointmentTypes[AppointmentTypes.Dienst]
          || appointment.type === AppointmentTypes[AppointmentTypes.MarktplatzDienst])
          && appointment.owner.email === this.sessionUser.getUser().email
          && this.myShiftsCounter <= 5
          && appointment.from.getTime() > new Date().getTime()
          ) {
          this
            .myShifts
            .push(appointment);
          this.myShiftsCounter++;
        }
      }
      if (this.sessionUser.getUser().entitlements.includes(Roles[Roles.AdminBfk])
      || this.sessionUser.getUser().entitlements.includes(Roles[Roles.UserBfk])) {
        if (appointment.type !== AppointmentTypes[AppointmentTypes.Dienst]
          && appointment.type !== AppointmentTypes[AppointmentTypes.MarktplatzDienst]
          && appointment.type !== AppointmentTypes[AppointmentTypes.FreierDienst]) {
          if (User.checkparticipations(appointment.participations[0])) {
            if (appointment.participations.includes(this.sessionUser.getUser().email)
            && this.myAppointmentsCounter <= 5
            && appointment.from.getTime() > new Date().getTime()) {
              this
                .myAppointments
                .push(appointment);
              this.myAppointmentsCounter++;
            }
          } else {
            if (this.checkForParticipationInOrg(appointment)
            && this.myAppointmentsCounter <= 5
            && appointment.from.getTime() > new Date().getTime()) {
              this
                .myAppointments
                .push(appointment);
              this.myAppointmentsCounter++;
            }
          }
        }
      }
    });
  }
  checkForParticipationInOrg(appointment: Appointment): boolean {
    for (const participant of appointment.participations) {
      for (const membership of this.sessionUser.getUser().memberships) {
        if (_.isEqual(participant, membership)) {
          return true;
        }
      }
    }
  }

}
